import noData from '@/components/noData'
import back from '@/components/back'

export default {
	components: {
		noData,
		back
	},
	data() {
		return {
			page: 1,
			list: null,
			order: [],
			user: this.$store.state.userInfo,
			showConfirm: false,
			haveMore: true,
			pageShow: false,
			tabItem: [{
				name: "我的服务包",
				val: 1
			}, {
				name: "服务包模板",
				val: 2
			}],
			active: {
				name: "我的服务包",
				val: 1
			},
			inputFlag: false,
			search: "",
			idx: null,
			choose: null,
			scrollTopDistance: 0,
		}
	},
	created() {
		this.$store.dispatch('pushservice', null)
		this.$store.dispatch('pushserviceCont', null)
		this.$store.dispatch('pushSaveDiease', null)
		this.$store.dispatch('pushDisease', [])
		this.$store.dispatch('pushcustomSevice',[])
		this.$store.dispatch('pushpreList',[])
		this.$store.dispatch('pushserviceBase', null)
		this.$store.dispatch('pushserviceFollowUp', null)
		this.pull(false)

	},
	mounted() {
		this.getMore()
		this.scrollIos()
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前    	
		next(vm => {
			vm.getMore()
			let flag = false
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh) {
				flag = true
			}
			if(flag) {
				vm.$store.dispatch('pushservice', null)
				vm.$store.dispatch('pushserviceCont', null)
				vm.$store.dispatch('pushSaveDiease', null)
				vm.$store.dispatch('pushDisease', [])
				vm.$store.dispatch('pushcustomSevice',[])
				vm.$store.dispatch('pushpreList',[])
				vm.$store.dispatch('pushserviceBase', null)
				vm.$store.dispatch('pushserviceFollowUp', null)
				vm.page = 1
				vm.list = null
				vm.haveMore = true
				vm.pageShow = false
				vm.active = {
					name: "我的服务包",
					val: 1
				}
				vm.idx = null
				vm.choose = null
				vm.search = ""
				vm.scrollTopDistance = 0
				vm.$store.dispatch('pushIsFresh', true)
				vm.pull(false)
			} else {
				setTimeout(() => {
					vm.$refs.el3.scrollTop = to.meta.scollTopPosition
				}, 1)
			}

		});
	},
	beforeDestroy() {
		window.onscroll = null
		this.showConfirm = false
	},
	methods: {
		searchFor() {
			this.list = null
			this.page = 1
			this.pull(false)
		},
		tabOn(i) {
			this.active = i
			this.idx = null
			this.choose = null
			this.page = 1
			this.haveMore = true
			this.pageShow = false
			this.list = null
			this.search = ""
			this.pull(false)
		},
		getShelf(i) {
			this.$get('PlatFormAPI/ServicePackItem/QueryServicePackTempletAndDetail',{
			    PackTempletId: i.PackTempletId,
				DrId:this.user.Data.User.DoctorId,
			}).then(o => {
			    this.$store.dispatch('pushDisease', [])
			    this.$store.dispatch('pushserviceBase', null)
				this.$store.dispatch('pushcustomSevice',[])
				this.$store.dispatch('pushpreList',[])
			    this.$store.dispatch('pushservice', o)
			    this.$store.dispatch('pushserviceCont', null)
			    this.$router.push(`/create-service-base`)
			})
		},
		pull(h) {
			let _url = ""
			let _parma = null
			if(this.active && this.active.val == 1) {
				//我的个性化服务包
				_url = 'PlatFormAPI/ServicePackItem/QueryDrPackPageAndDetail'
				_parma = {
					ServicePackName: this.search,
					DrId: this.user.Data.User.DoctorId,
					Invalid: 0, //0-上架，1-下架(-1查询所有)
					PackStatus: -1, //服务包状态(0-审核中，1-已审核，2-审核不通过),-1查询所有 如果是患者查询购买服务包赋值已审核
					IsRecommend:null,//是否可以推荐(0-否，1-是),null查询所有
					PageIndex: this.page,
					PageSize: 15
				}
			} else if(this.active && this.active.val == 2) {
				//我的服务包模板
				_url = 'PlatFormAPI/ServicePackItem/QueryServicePackTempletByPage'
				_parma = {
					TempletType: -1,
					PackTempletName: this.search,
					ServiceClassId: -1, //服务类别Id
					PageIndex: this.page,
					PageSize: 15,
				}
			} else {
				_url = 'PlatFormAPI/ServicePackItem/QueryDrPackPageAndDetail'
				_parma = {
					ServicePackName: this.search,
					DrId: this.user.Data.User.DoctorId,
					Invalid: 0, //0-上架，1-下架(-1查询所有)
					PackStatus: -1, //服务包状态(0-审核中，1-已审核，2-审核不通过),-1查询所有 如果是患者查询购买服务包赋值已审核
					IsRecommend:null,//是否可以推荐(0-否，1-是),null查询所有
					PageIndex: this.page,
					PageSize: 15
				}
			}
			this.$post(_url, _parma).then(o => {
				this.pageShow = true
				if(!o.ReturnData || o.ReturnData.length < 15) {
					this.haveMore = false
				} else {
					this.haveMore = true
				}

				if(h) {
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach(v => {
						v.isExtend = false
						let details = v.Details || []
						let a = null //随访
						let b = null //问卷
						if(details && details.length){
							details.forEach((n,m) => {
								/*
								1701:问卷
								1801:随访计划
								 * */
								if(n.SericeItemCode == '1801'){
									a = JSON.parse(JSON.stringify(n))
									n.isShow = false
								}else if(n.SericeItemCode == '1701'){
									b = JSON.parse(JSON.stringify(n))
									n.isShow = false
								}else{
									n.isShow = true
								}
							})
						}
						if(a){
							a.isShow = true
							details.push(a)
						}
						if(b){
							b.isShow = true
							details.push(b)
						}
						v.Details = details
						this.list.push(v)
					})
				} else {
					o.ReturnData = o.ReturnData || []
					o.ReturnData.forEach((v, k) => {
						if(k == 0) {
							o.ReturnData[k].isExtend = true
						} else {
							o.ReturnData[k].isExtend = false
						}
						let details = v.Details || []
						let a = null //随访
						let b = null //问卷
						if(details && details.length){
							details.forEach((n,m) => {
								/*
								1701:问卷
								1801:随访计划
								 * */
								if(n.SericeItemCode == '1801'){
									a = JSON.parse(JSON.stringify(n))
									n.isShow = false
								}else if(n.SericeItemCode == '1701'){
									b = JSON.parse(JSON.stringify(n))
									n.isShow = false
								}else{
									n.isShow = true
								}
							})
							
						}
						if(a){
							a.isShow = true
							details.push(a)
						}
						if(b){
							b.isShow = true
							details.push(b)
						}
						o.ReturnData[k].Details = details
					})
					this.list = o.ReturnData || []
					this.$refs.el.scrollTop = 100
				}
			})
		},
		getInvalid(i, idx) {
			//下架服务包
			if(i.Invalid == 0) {
				this.showConfirm = true
				this.idx = idx
				this.choose = i
			}
		},
		extend(i,idx){
    		this.list[idx].isExtend = !this.list[idx].isExtend
    	},
    	goDetail(i){
            this.$store.dispatch('pushDisease', [])
            this.$store.dispatch('pushserviceBase', null)
            if(i.PackStatus == 1){
                return this.$router.push(`/package-detail?serviceId=${i.ServicePackId}`)
            }
            else{
                this.$store.dispatch('pushDisease', [])
				this.$store.dispatch('pushcustomSevice',[])
				this.$store.dispatch('pushpreList',[])
				this.$store.dispatch('pushserviceBase', null)
				this.$store.dispatch('pushservice', i)
				return this.$router.push('/create-service-base')
            }   
        },
		confirm() {
			this.$post('PlatFormAPI/ServicePackItem/PackIsDisplay', {
				ServicePackId: this.choose.ServicePackId,
				Invalid: 1,
			}).then(o => {
				//this.list[this.idx].Invalid = 1
				this.list.splice(this.idx, 1)
				this.$toast('下架成功')
			})
		},
		 toCreate(){
		    this.$store.dispatch('pushDisease', [])
		    this.$store.dispatch('pushserviceBase', null)
		    this.$store.dispatch('pushserviceCont', null)
		    this.$store.dispatch('pushcustomSevice',[])
		    this.$store.dispatch('pushpreList',[])
			this.$store.dispatch('pushserviceQuestionnaire',null)
			this.$store.dispatch('pushserviceFollowUp',null)
			this.$store.dispatch('pushservice',null)
		    this.$router.push('/create-service-base')
		},
		getMore() {

			const el3 = this.$refs.el3
			let timer2 = null
			el3.onscroll = () => {
				clearTimeout(timer2)
				timer2 = setTimeout(() => {
					const top = el3.scrollTop ||
						el3.pageYOffset ||
						el3.scrollTop,
						h = el3.scrollHeight,
						wh = window.innerHeight
					this.scrollTopDistance = top;
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.pull(true)
					}
				}, 100)
			}
		},
		scrollIos(){
			//ios页面卡死问题
			let el3 = this.$refs.el3
			let lastY = 0;
			el3.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el3
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
	}
}



